  componentDidMount = async () => {
    if (window.ethereum) {
      window.ethereum.on("chainChanged", () => {
        window.location.reload();
      });
      window.ethereum.on("accountsChanged", () => {
        window.location.reload();
      });
    }

    try {
      // Get network provider and web3 instance.
      const web3 = await getWeb3();
      if (!window.web3) {
        window.alert("Please install MetaMask first.");
        return;
      }
      // Use web3 to get the user's accounts.
      const accounts = await web3.eth.getAccounts();

      // Get the contract instance.
      const networkId = await web3.eth.net.getId();
      if (networkId !== 137) {
        window.alert(`Please Use Polygon Network`);
      }

      const deployedNetwork = KOIProject.networks[137];
      const instance = new web3.eth.Contract(
        KOIProject.abi,
        deployedNetwork && deployedNetwork.address
      );

      const deployedNetwork_AURA = AURAWrapped.networks[137];
      const instance_AURA = new web3.eth.Contract(
        AURAWrapped.abi,
        deployedNetwork_AURA && deployedNetwork_AURA.address
      );

      // Set web3, accounts, and contract to the state, and then proceed with an
      // example of interacting with the contract's methods.
      this.setState({ web3, accounts, contract: instance, contract_AURA: instance_AURA }, this.runExample);
    } catch (error) {
      // Catch any errors for any of the above operations.
      alert(
        `Failed to load web3, accounts, or contract. Check console for details.`
      );
      console.error(error);
    }
  };

