    console.log("accounts:" + accounts);

    //  console.log(useState["en"]);

    this.setState({ GetNetworkId: _GetNetworkId });
    if (_GetNetworkId === 137) {
      var accountStr = accounts[0];
      accountStr = accountStr.substr(0, 6) + "..." + accountStr.substr(-6);

      this.setState({ NetWorkState: accountStr });
    } else {
      this.setState({ NetWorkState: "Connect wallet" });
    }

    const _currentPrice = await contract.methods.getPrice().call();
    const _Balance = await contract.methods.balanceOf(accounts[0]).call();
    const _KOI_Provenance = await contract.methods.KOI_Provenance().call();
    const _symbol = await contract.methods.symbol().call();
    const _totalSupply = await contract.methods.totalSupply().call();
    const _AURAtotalSupply = await contract.methods.AURAtotalSupply().call();



    // if (this.state.YourKois) {

    //   var _YourKois_index = 0
    //   var _YourKoi_id
    //   for (_YourKois_index; _YourKois_index < _YourKois.length; _YourKois_index++) {
    //     _YourKoi_id = _YourKois[_YourKois_index];
    //     _KOIDate = await contract.methods.KOI_Profile(_YourKoi_id).call();
    //     if (_KOIDate) {

    //       _AURA[_YourKoi_id] = _KOIDate['_AURA'];
    //       _level[_YourKoi_id] = _KOIDate['_level'];
    //       _Luckymoney[_YourKoi_id] = _KOIDate['_Luckymoney'];
    //       _log[_YourKoi_id] = _KOIDate['_log'];
    //       _Gender[_YourKoi_id] = _KOIDate['_Gender'];
    //       _TransferTimes[_YourKoi_id] = _KOIDate['_TransferTimes'];
    //       _KOIDate = [];
    //       console.log(_YourKoi_id);
