    // }


    this.setState({ currentPrice: _currentPrice });
    this.setState({ Balance: _Balance });
    this.setState({ KOI_Provenance: _KOI_Provenance });
    this.setState({ symbol: _symbol });
    this.setState({ totalSupply: _totalSupply });
    this.setState({ show: false });
    this.setState({ AURAtotalSupply: _AURAtotalSupply / 1e18 });

    // // Update state with the result.
    // if(_level.length==_YourKois.length){
    // this.setState({ Player_AURA: _AURA });
    // this.setState({ Player_level: _level });
    // this.setState({ Player_Luckymoney: _Luckymoney });
    // this.setState({ Player_log: _log });
    // this.setState({ Player_Gender: _Gender });
    // this.setState({ Player_TransferTimes: _TransferTimes });
    // console.log(this.state.Player_AURA);

    // const _MaticBalance = await  accounts[0].getBalance()
    // this.setState({ MaticBalance: _MaticBalance});
    //}
