  handleSubmit = async (event) => {
    //event.preventDefault();
    console.log(this.state.accounts[0]);

    // await this.state.contract.Get_ricj_All.send({ from: this.accounts[0] });
    if (this.state.MintAmount * this.state.currentPrice > 0) {
      await this.state.contract.methods.mint(this.state.MintAmount).send({
        value: this.state.MintAmount * this.state.currentPrice,
        from: this.state.accounts[0],
        _count: this.state.MintAmount,
      });
      this.setState({ MintAmount: 0 });
    }
  };


  levelupthis = async (e, KOI_id) => {
    await this.state.contract.methods.level_up(KOI_id).send({ from: this.state.accounts[0] });
  }
  CollectLuckymoneythis = async (e, KOI_id) => {
    await this.state.contract.methods.Get_rich(KOI_id).send({ from: this.state.accounts[0] });

  }
  CollectAurathis = async (e, KOI_id) => {
    var _AURAclaimable = '';
    _AURAclaimable = await this.state.contract.methods.AURAclaimable(KOI_id).call();
    await this.state.contract.methods.AURAclaim(KOI_id, _AURAclaimable).send({ from: this.state.accounts[0] });
  }

  WrapAuraThis = async (e, KOI_id) => {
    var _AURA = '';
    _AURA = await this.state.contract.methods.AURA(KOI_id).call();
    if (_AURA > 0) {
      this.setState({ WrapAuraState: "Waiting for Confirmation" })
      this.setState({ Player_WrapAura: true });
      await this.state.contract.methods.AURAapprove(KOI_id, 17, _AURA).send({ from: this.state.accounts[0] });
      this.setState({ WrapAuraState: "Ready to Wrap" })
      await this.state.contract_AURA.methods.wrap(KOI_id, _AURA).send({ from: this.state.accounts[0] });
      this.setState({ WrapAuraState: "Wrap Aura Done" })



    }

  }

  KOI_Profils = async (e, KOI_id) => {
    //event.preventDefault();
    // console.log(event);
    // var KOI_id = event.value;
    var _AURA = '';
    var _level = '';
    var _Luckymoney = '';
    var _log = '';
    var _Gender = '';
    var _TransferTimes = '';
    var _KOIDate = [];
    var _now = new Date().getTime() / 1000;
    var _LevelUpable = true;
    var _AURAClaimable = true;
    var _LuckyMoneyCollectable = true;
    var _WrapAura = true;
    var Luckymoney_required = '';
    _KOIDate = await this.state.contract.methods.KOI_Profile(KOI_id).call();
    if (_KOIDate) {
      _AURA = _KOIDate['_AURA'] / 1e18;
      _level = _KOIDate['_level'];
      _Luckymoney = _KOIDate['_Luckymoney'] / 1e18;
      _log = _KOIDate['_log'];
      Luckymoney_required = await this.state.contract.methods.Luckymoney_required(_level).call();
      if (_KOIDate['_Gender'] === '0') {
        _Gender = 'F'
      }
      else {
        _Gender = 'M'
      };
      _TransferTimes = _KOIDate['_TransferTimes'];
      _KOIDate = [];

      if (_now > _log) {
        _LuckyMoneyCollectable = false;
      }

      if (_level > 1 && _Luckymoney > 7 * 1e18) {

        _AURAClaimable = false;
      }

      if (_Luckymoney > Luckymoney_required) {
        _LevelUpable = false;

      }

      if (_AURA > 0) {
        _WrapAura = false;
      }

      this.setState({ Player_KOI_id: KOI_id });
      this.setState({ Player_AURA: _AURA });
      this.setState({ Player_level: _level });
      this.setState({ Player_Luckymoney: _Luckymoney });
      this.setState({ Player_log: _log });
      this.setState({ Player_Gender: _Gender });
      this.setState({ Player_TransferTimes: _TransferTimes });
      this.setState({ Player_LuckyMoneyCollectable: _LuckyMoneyCollectable });
      this.setState({ Player_AURAClaimable: _AURAClaimable });
      this.setState({ Player_LevelUpable: _LevelUpable });
      this.setState({ Player_WrapAura: _WrapAura });
      this.setState({ WrapAuraState: "Wrap Aura" })

    }

    console.log(_AURA + '/' + _level + '/' + _Luckymoney + '/' + _log + '/' + _Gender + '/' + _TransferTimes)
    this.handleShow();
  };


  handleMintAmountChange = (evt) => {
    if (rx_live.test(evt.target.value)) {
      this.setState({ MintAmount: evt.target.value });
    }
    if (evt.target.value > 20) {
      this.setState({ MintAmount: 20 });
    }
  };

  //

  setShow = (_bool) => {
    this.setState({ show: _bool });
  }
  handleClose = () => { this.setShow(false) };
  handleShow = () => { this.setShow(true) };


